import React from "react"
import { Row, Col } from "antd"
import { useLocation } from "react-router-dom"
import { newClient } from "helpers"

import { Layout } from "components/common/LessonVideo"
import ListVideo from "./ListVideo"
import { LessonContext } from "./index"

import { FIND_LESSON_VIDEO } from "queries/lessonVideo"

const cudyLesson = newClient("cudy_lesson")

function SearchLessonVideo() {
	const { search } = useLocation()

	const params = new URLSearchParams(search)
	const searchParams = params.get("q"),
		curriculumPvid = params.get("curriculumPvid"),
		subjectPvid = params.get("subjectPvid")

	return (
		<LessonContext.Provider value={{ client: cudyLesson }}>
			<Layout>
				<Row gutter={[24, 24]}>
					<Col span={14}>
						<ListVideo
							span={24}
							gqlQuery={FIND_LESSON_VIDEO}
							listData="findVideoLesson"
							keyword={searchParams}
							curriculumPvid={curriculumPvid}
							subjectPvid={subjectPvid}
						/>
					</Col>
					<Col span={10}>For Tutor</Col>
				</Row>
			</Layout>
		</LessonContext.Provider>
	)
}

export default SearchLessonVideo
