import { gql } from "apollo-boost"

export const LIST_VIDEO_LESSON = gql`
	query LIST_VIDEO_LESSON($country: String!) {
		listVideoLesson(country: $country) {
			pvid
			youtubeVideoId
			title
			curriculumName
			subjectName
			description
			duration
			youtubeLink
			embedLink
			channelId
			channelTitle
			channelLink
			publishedAt
			thumbnail
			country
			createdAt
		}
	}
`

