export const LIST_ANOTHER_VIDEO = gql`
	query LIST_ANOTHER_VIDEO($youtubeVideoId: String!, $country: String!, $curriculumPvid: Int!, $subjectPvid: Int!) {
		listAnotherVideo(youtubeVideoId: $youtubeVideoId, country: $country, curriculumPvid: $curriculumPvid, subjectPvid: $subjectPvid) {
			pvid
			youtubeVideoId
			title
			curriculumName
			subjectName
			description
			duration
			youtubeLink
			embedLink
			channelId
			channelTitle
			channelLink
			publishedAt
			thumbnail
			country
			createdAt
		}
	}
`
export const DETAIL_VIDEO_LESSON = gql`
	query DETAIL_VIDEO_LESSON($youtubeVideoId: String!) {
		detailVideoLesson(youtubeVideoId: $youtubeVideoId) {
			pvid
			youtubeVideoId
			title
			curriculumName
			subjectName
			description
			duration
			youtubeLink
			embedLink
			channelId
			channelTitle
			channelLink
			publishedAt
			thumbnail
			country
			createdAt
			favourite
			curriculumPvid
			subjectPvid
			comments {
				pvid
				comment
				countReplied
				createdAt
				user {
					userPvid
					token
					email
					country
					firstName
					lastName
					resource {
						path
					}
				}
			}
		}
	}
`

export const FIND_LESSON_VIDEO = gql`
	query FIND_LESSON_VIDEO($keyword: String) {
		findVideoLesson(
			curriculumPvid: 15
			subjectPvid: 15
			keyword: $keyword
			country: "id"
			isDemo: true
			duration: "short"
			sortBy: ""
			loadMore: false
		) {
			pvid
			youtubeVideoId
			title
			curriculumName
			subjectName
			description
			duration
			youtubeLink
			embedLink
			channelId
			channelTitle
			channelLink
			publishedAt
			thumbnail
			country
			createdAt
		}
	}
`

