import React, { useContext } from "react"
import { Link, useParams, useLocation } from "react-router-dom"
import { Row, Col, Divider } from "antd"
import { useQuery } from "@apollo/react-hooks"
import { LessonContext } from "./index"
import { Heading } from "components"
import { TumbVideo, Section } from "components/common/LessonVideo"

function ListVideo({ header, subHeader, isSlice = false, isEnd = false, keyword, span, listData, gqlQuery, ...props }) {
	const { client } = useContext(LessonContext)
	const { country } = useParams()

	const location = useLocation()

	const gqlOpts = {
		client,
		variables: {
			country,
			youtubeVideoId: props.youtubeVideoId,
			curriculumPvid: parseInt(props.curriculumPvid),
			subjectPvid: parseInt(props.subjectPvid),
			keyword
		}
	}
	// alert(JSON.stringify(props, null, null, 2))

	const { data, loading } = useQuery(gqlQuery, gqlOpts)
	// change to skaleton

	if (loading) {
		return <div>This Content is loading</div>
	}
	const urlPath = location.pathname.split("/")[3]
	// console.log(keyword)
	const linkTo = (youtubeVideoId) => {
		if (urlPath === "search") return `watch?v=${youtubeVideoId}`
		return urlPath !== "watch" && urlPath !== "" ? `lesson/watch?v=${youtubeVideoId}` : `watch?v=${youtubeVideoId}`
	}

	return (
		<Section>
			<Row gutter={[24, 24]}>
				{!!header && (
					<Col span={24}>
						<Heading content={header} subheader={subHeader} />
					</Col>
				)}

				{/* Check if slice  */}
				{isSlice &&
					data[listData].slice(0, 8).map((video) => (
						<Col span={!!span ? 24 : 6}>
							<Link to={() => linkTo(video.youtubeVideoId)}>
								<TumbVideo data={video} />
							</Link>
						</Col>
					))}

				{/* if not slice */}
				{!isSlice &&
					data[listData].map((video) => (
						<Col span={!!span ? 24 : 6}>
							<Link to={() => linkTo(video.youtubeVideoId)}>
								<TumbVideo data={video} />
							</Link>
						</Col>
					))}

				{/*if is end don't have hr */}
				{!isEnd && (
					<Col span={24}>
						<Divider style={{ height: "5px" }} />
					</Col>
				)}
			</Row>
		</Section>
	)
}

export default ListVideo
