import React from "react"
import { Card as CardAntd, Tooltip } from "antd"
import styled from "styled-components"

const Card = styled(CardAntd)`
	.ant-card-cover {
		overflow: hidden;
		height: auto;
		.img-tumbnail {
			width: 100%;
			margin: -1.5rem 0px;
		}
	}
	.ant-card-body {
		padding: 0px !important;
		background-color: #f0f2f5;
		.ant-card-meta-detail {
			margin-top: 1rem;
		}
	}
`

const { Meta } = Card

function TumbVideo({ data, ...props }) {
	const { pvid, thumbnail, title, duration, curriculumName, subjectName } = data
	return (
		<Card cover={<img alt={title} src={thumbnail} className="img-tumbnail" />} bordered={false}>
			<Description desc={{ title, curriculumName, subjectName }} />
		</Card>
	)
}

export default TumbVideo

const Description = ({ desc }) => {
	const { title, curriculumName, subjectName } = desc
	const description = (
		<div>
			<span className="curriculumName">{curriculumName}</span> | <span className="subjectName">{subjectName}</span>
		</div>
	)
	return <Meta title={title} description={description} />
}
