import React, { createContext } from "react"
import {Helmet} from 'react-helmet'
import { newClient } from "helpers"

import { Layout } from "components/common/LessonVideo"
import ListVideo from "./ListVideo"

import { LIST_VIDEO_LESSON, LIST_RECOMMENDED_FAVOURITE } from "queries/lessonVideo"

const cudyLesson = newClient("cudy_lesson")

function LessonVideo() {
	// console.log(localStorage.getItem("token"));
	const [title,setTitle] = React.useState("Cudy Lesson")
	document.title = title

	return (
		<LessonContext.Provider value={{ client: cudyLesson }}>
			<Helmet>
				<title>{title}</title>
			</Helmet>
			<Layout>
				<ListVideo isSlice gqlQuery={LIST_VIDEO_LESSON} listData="listVideoLesson" />
				<ListVideo
					isSlice
					gqlQuery={LIST_RECOMMENDED_FAVOURITE}
					listData="listRecommendedVideo"
					header="Recommended Videos"
					subHeader="A lot of video that is very recommended for you"
				/>
			</Layout>
		</LessonContext.Provider>
	)
}
export default LessonVideo

export const LessonContext = createContext()
