import React from "react"
import { Form } from "formik-antd"
import { Formik } from "formik"
import { useLocation, Link, useHistory } from "react-router-dom"
import { Row, Col, Input } from "antd"

import Logo from "components/Logo"
import {Modal} from 'components'

function Navbar() {
	const { Search } = Input
	const { push } = useHistory()

	const location = useLocation()

	const lessonPage = location.pathname.split("/").slice(1, 3).join("/")
	const handleSearch = (values, action) => {
		action.setSubmitting(false)
	}
	const handleSearchInput = (values) => {
		push(`/${lessonPage}/search?q=${values}&subjectPvid=15&curriculumPvid=15`)
	}
	const [isModalShow, setIsModalShow] = React.useState(false)

	return (
		<Row gutter={[24, 24]} style={{ display: "flex", alignItems: "center" }} align="middle">
			<Col span={4}>
				<Link to={`/${lessonPage}`}>
					<Logo mb={0} />
				</Link>
			</Col>
			<Col span={12}>
				<Row align="middle">
					<Col span={24}>
						<Formik
							onSubmit={handleSearch}
							initialValues={{ q: "" }}
							render={({ handleSubmit }) => (
								<Form onSubmit={handleSubmit}>
									<Search placeholder="Find by keyword" onSearch={handleSearchInput} enterButton name="q" />
								</Form>
							)}
						/>
					</Col>
				</Row>
			</Col>
			<Col span={8}>
				<Row justify="end">
					<Col span={12} style={{ textAlign: "center" }} onClick={() => setIsModalShow(!isModalShow)}>
						Preferences
					</Col>
					<Modal title="Preferences" visible={isModalShow} onCancel={() => setIsModalShow(!isModalShow)}>
						Ok
					</Modal>
					<Col span={12} style={{ textAlign: "center" }}>
						Account
					</Col>
				</Row>
			</Col>
		</Row>
	)
}

export default Navbar
